import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { getMenuNews, getMenuSearch } from "../../api/menu";
import MediaImage from "../../components/features/product/MediaImage";
import market from "../../config/market";
import { debounce } from "../../utils/debounce";
import { types } from "./router";

export default function BlogLeftSide({ blogs, setResult, setLoader }) {
	if (!blogs) return "";
	const lang = window.localStorage.getItem("lang");
	const publikation = useSelector((state) => state.any.blogs);
	const [menu, setMenu] = useState();
	const [query, setQuery] = useState();

	const { t } = useTranslation();
	function searchFn(value) {
		setLoader(true);
		debounce(
			() => {
				setQuery(value);
				setResult({
					data: [],
				});
				setLoader(false);
			},
			"inputQuery",
			500
		);
	}
	useEffect(() => {
		getMenuNews(blogs[0]?.type).then((res) => {
			setMenu(res.items);
		});
	}, [blogs, query]);

	useEffect(() => {
		if (query) {
			getMenuSearch(blogs[0]?.type, query).then((res) => {
				setResult(res.items);
			});
			setQuery("");
		}
	}, [blogs, query]);

	return (
		<div className="left-side">
			<div className="title">
				<h3>
					{
						types.find(
							(type) =>
								type.key ===
								window.location.pathname.split("/")[1]
						)?.label
					}
				</h3>
			</div>
			<div className="search">
				<form>
					<input
						onChange={(e) => {
							searchFn(e.target.value.trim());
						}}
						type="search"
						className={`form-control`}
						placeholder={t("search-products")}
					/>
				</form>
			</div>
			<ul className="menu-list">
				{menu?.map((item, index) => (
					<li className="menu-item" key={index}>
						<Link
							className="menu-link"
							to={`/${
								types.find((type) => type.value === item.type)
									.key
							}`}
							onClick={() => setQuery("")}
						>
							<span>
								{
									item.translations.find(
										(langu) => langu.locale === lang
									)?.name
								}
							</span>
							<img src={market.config.icons.rArrow} alt="arrow" />
						</Link>
					</li>
				))}
			</ul>
			<div className="lates-content">
				<div className="title">
					<h3>Последние</h3>
				</div>
				{publikation?.slice(0, 4).map((item, index) => (
					<div className="blog-content" key={index}>
						<div className="blog-img">
							<MediaImage
								image={item.image}
								alt="blog-imgage"
								largeSize="800x_"
								mediumSize="800x_"
								smallSize="800x_"
							/>
						</div>
						<div className="title">
							<Link
								to={`/${
									types.find(
										(type) => type.value === item.type
									)?.key
								}/${item.id}`}
							>
								{
									item.translations.find(
										(langu) => langu.locale === lang
									)?.title
								}
							</Link>
						</div>
					</div>
				))}
			</div>
		</div>
	);
}
