import React, { useState } from "react";
import { useSelector } from "react-redux";
import Breadcrumb from "../../components/common/breadcrumb";
import BlogLeftSide from "./LeftSide";
import BlogRightSide from "./BlogRightSide";
import { types } from "./router";

export default function BlogsList(props) {
	const publikation = useSelector(state => state.any.blogs);
	const [result, setResult] = useState();
	const [loader, setLoader] = useState(false);
	const blogs = publikation?.filter(
		blog =>
			blog.type ===
			types.find(type => type.key === props.match.path.split("/")[1])
				.value
	);
	const menuName = types.find(
		type => type.key === props.match.path.split("/")[1]
	);

	return (
		<div className="blog-page">
			<div className="header">
				<Breadcrumb
					breadcrumb={[
						{ link: `/${menuName.key}`, name: menuName.label }
					]}
				/>
			</div>
			<div className="container">
				<div className="row side-row">
					<div className="col-md-3 pl-md-0">
						<BlogLeftSide
							blogs={blogs}
							setResult={setResult}
							setLoader={setLoader}
						/>
					</div>
					<div className="col-md-9 right-side">
						<BlogRightSide
							blogs={blogs}
							result={result}
							loader={loader}
						/>
					</div>
				</div>
			</div>
		</div>
	);
}
