import React from "react";
import { Link } from "react-router-dom";
import { postNewsStatistic } from "../../api/statistic";
import LoaderOverlay from "../../components/features/loaders/LoaderOverlay";
import MediaImage from "../../components/features/product/MediaImage";

export default function BlogRightSide({ blogs, result, loader }) {
	const lang = window.localStorage.getItem("lang");
	if (!blogs) {
		return "";
	}

	return loader ? (
		<LoaderOverlay wrapper={true} />
	) : (
		(result && result.length > 0 ? result : blogs)
			?.sort(function (a, b) {
				return a.sort - b.sort;
			})
			.map((item, index) => (
				<div
					className="blog-content"
					key={index}
					onClick={() => {
						postNewsStatistic(item.id);
					}}
				>
					<div className="blog-img">
						<MediaImage
							image={item.image}
							alt="blog-imgage"
							largeSize="800x_"
							mediumSize="800x_"
							smallSize="800x_"
						/>
					</div>

					<div className="title-content">
						<Link
							to={`/${window.location.pathname.split("/")[1]}/${
								item.id
							}`}
						>
							{
								item.translations.find(
									(langu) => langu.locale === lang
								).title
							}
						</Link>
						<p>
							{
								item.translations.find(
									(langu) => langu.locale === lang
								).seoHeader
							}
						</p>
						<div
							className="content"
							dangerouslySetInnerHTML={{
								__html: item.translations.find(
									(langu) => langu.locale === lang
								).seoDescription,
							}}
						/>
					</div>
				</div>
			))
	);
}
