import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import Breadcrumb from "../../components/common/breadcrumb";
import BlogLeftSide from "./LeftSide";
import MediaImage from "../../components/features/product/MediaImage";
import { types } from "./router";

export default function BlogPage(props) {
	const blogs = useSelector(state => state.any.blogs);

	const lang = window.localStorage.getItem("lang");

	const postId = parseInt(props.match.params.id);

	const menuName = types.find(
		type => type.key === props.match.path.split("/")[1]
	);

	const [post, setpost] = useState();

	useEffect(() => {
		setpost({ ...blogs?.find(i => i.id === postId) });
	}, [postId]);

	if (!post) {
		return "";
	}
	return (
		<div className="blog-main">
			<div className="header">
				<Breadcrumb
					breadcrumb={[
						{ link: `/${menuName.key}`, name: menuName.label }
					]}
				/>
			</div>
			<div className="container">
				<div className="row">
					<div className="col-md-3 pl-md-0">
						<BlogLeftSide blogs={blogs} />
					</div>
					<div className="col-md-9 right-side">
						<div className="title">
							<h2>
								{
									post.translations.find(
										langu => langu.locale === lang
									)?.title
								}
							</h2>
							<p>
								<span>Дата:</span>
								{post.createdTime
									?.split(" ")[0]
									.replace(/[-]/g, "/")}
							</p>
						</div>
						<div className="blog-img">
							<MediaImage
								id={post.id}
								image={post.image}
								alt="blog-imgage"
								largeSize="800x_"
								mediumSize="800x_"
								smallSize="800x_"
							/>
						</div>
						<div
							className="content"
							dangerouslySetInnerHTML={{
								__html: post.translations.find(
									langu => langu.locale === lang
								)?.content
							}}
						/>
					</div>
				</div>
			</div>
		</div>
	);
}
