import React from "react";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { useTranslation } from "react-i18next";
import { dt } from "../../i18n";

function Breadcrumb(props) {
	const pages = props.pages ? props.pages : [];
	let prev;
	let next;
	for (let [index, page] of pages.entries()) {
		if (parseInt(props.productId) === page.id) {
			prev = pages[index - 1] ? pages[index - 1] : pages[index];
			next = pages[index + 1] ? pages[index + 1] : pages[index];
		}
	}

	const { t } = useTranslation();

	return (
		<nav aria-label="breadcrumb" className={`breadcrumb-nav mb-2 w-100`}>
			<div className="container mx-auto">
				<ul className="breadcrumb">
					<li className="breadcrumb-item">
						<Link to="/">{t("home")}</Link>
					</li>
					{props.breadcrumb?.map((crumb, index) => (
						<li key={index} className="breadcrumb-item">
							<Link to={`${crumb.link}`}>{dt(crumb.name)}</Link>
						</li>
					))}
					{props.productName ? (
						<li className="breadcrumb-item">
							<Link to="">{dt(props.productName)}</Link>
						</li>
					) : null}

					{props.productId && prev && next ? (
						<nav
							className="product-pager ml-auto"
							aria-label="Product"
						>
							<Link
								className={`product-pager-link product-pager-prev ${prev.id ===
									parseInt(props.productId) && "d-none"}`}
								to={`/product/${prev.id}`}
								aria-label="Previous"
								tabIndex="-1"
							>
								<i className="icon-angle-left"></i>
								<button>{t("prev")}</button>
								{prev ? (
									<div className="product-detail">
										<figure>
											<img
												src={prev.image}
												alt="Product"
											/>
										</figure>
										<h3 className="product-name">
											{dt(prev.name)}
										</h3>
									</div>
								) : (
									""
								)}
							</Link>

							<Link
								className={`product-pager-link product-pager-next ${next.id ===
									parseInt(props.productId) && "d-none"}`}
								to={`/product/${next.id}`}
								aria-label="Next"
								tabIndex="-1"
							>
								<button>{t("next")}</button>

								<i className="icon-angle-right"></i>
								{next ? (
									<div className="product-detail">
										<figure>
											<img
												src={next.image}
												alt="Product"
											/>
										</figure>
										<h3 className="product-name">
											{dt(next.name)}
										</h3>
									</div>
								) : (
									""
								)}
							</Link>
						</nav>
					) : null}
				</ul>
			</div>
		</nav>
	);
}
function mapStateToProps(state, props) {
	return {
		pages: state.any.pages ? state.any.pages : [],
		props: props
	};
}

export default connect(mapStateToProps)(Breadcrumb);
